:root {
  --bs-heading-color: #424242 !important;
}

@font-face {
  font-family: "Outfit";
  src: url("../fonts/outfit/Outfit-VariableFont_wght.ttf");
}
body {
  font-family: "Outfit", sans-serif !important;
  font-optical-sizing: auto;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
}

#back-to-top {
  width: 10rem;
  right: 0;
  left: auto;
  padding-left: 0 !important;
}

.footer-logos {
  max-width: 400px;
}

#gallery img {
  max-height: 80vh;
}

body {
  font-size: 1.2rem;
}

h1 {
  margin-bottom: 3rem !important;
}